.wp-block-eedee-block-gutenslider .wp-block-eedee-gutenslider__content > .block-list-appender {
	margin: 0 0 0 40px;
}

.wp-block-eedee-block-gutenslider .wp-block-eedee-gutenslider__content .wp-block {
	max-width: none;
	margin-left: 0;
	margin-right: 0;
}

.editor-styles-wrapper .wp-block-eedee-block-gutenslide[data-block] {
	margin-top: 0;
	margin-bottom: 0;
	margin-left: 0;
	margin-right: 0;
	max-width: none;
}

.eedee-gutenslider-add-empty {
	margin-top: 20px;

	.block-editor-button-block-appender {
		flex-direction: row;
		box-shadow: none;
	}
}

.block-editor-block-list__layout {
	.slide-link {
		display: none;
	}

	.wp-block-eedee-block-gutenslider.content-fixed	.wp-block-eedee-gutenslider__content .block-list-appender {
		position: relative;
	}
}

.block-editor-block-list__layout .wp-block-eedee-block-gutenslider.content-fixed .wp-block-eedee-gutenslider__content {
	> *:last-of-type {
		margin-bottom: 0;
	}
}

.gutenslider-insert-slide {
	width: 100%;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
}

/* Gutenberg style fixes / hacks */
.interface-interface-skeleton__editor {
	overflow-y: hidden;
	// max-width: 0;
}

.editor-styles-wrapper .wp-block-eedee-block-gutenslider .wp-block {
	max-width: none;
}
/* end hacks */

.wp-block-eedee-block-gutenslider.content-change {
	.swiper-wrapper > .wp-block:not([data-type="eedee/block-gutenslide"]) {
		display: none;
	}
}

.wp-block-eedee-block-gutenslider.content-fixed {

	.block-editor-inserter__toggle.components-button.has-icon {
		display: block !important;
	}

	//show blocks in fixed edit mode
	.swiper-wrapper [data-type="eedee/block-gutenslide"] {
		display: block;
	}

	.fixed-content-edit {
		z-index: 5;

		// but not in the block editor window
		[data-type="eedee/block-gutenslide"] {
			display: none;
		}
	}

	.gutenslider-preview {
		position: relative;
		display: grid;
		flex-direction: column;
		justify-content: center;
	}
}

/** editing controls **/
.with-unit-buttons {
	display: flex;
	flex-direction: row-reverse;
	padding-right: 12px;
}

.unit-buttons {
	position: absolute;
	right: 18px;

	button {
		font-size: 12px;
		padding: 0;
		margin-left: 10px;

		&:hover {
			background: #eee;
			border-color: #00435d;
		}
	}

	.is-toggled {
		border-color: #006a95 #00648c #00648c;
		border-bottom: 1px solid;
	}
}

.gutenslider-toggle-edit-mode {
	padding-top: 10px;
}

.components-button-group.gutenslider-toggle-fade-mode {
	.components-button {
		display: flex;
		justify-content: center;
	}
}

.wp-block-eedee-block-gutenslider {
	.components-button.components-icon-button.block-list-appender__toggle {
		display: none;
	}
}

.components-button-group.gutenslider-toggle-adaptive-height,
.components-button-group.gutenslider-toggle-content-mode,
.components-button-group.gutenslider-toggle-fade-mode {
	margin-bottom: 8px;
	display: flex;

	.components-button {
		padding: 6px 8.5px;
		flex: 1;
	}
}

.components-button-group.gutenslider-toggle-fade-mode {
	margin: 0 !important;
}



.gutenslider-controls {

	.components-base-control__field {
		margin-bottom: 8px;
	}

	.components-base-control {
		margin-bottom: 0;
	}

	.components-button-group {
		margin-top: 3px;
	}

	.gutenslider-toggle-adaptive-height {
		margin-top: 16px;
		margin-bottom: 16px;
	}

	.components-base-control__field .components-circular-option-picker {
		padding-top: 8px;
	}

	.components-range-control__mark-label {
		top: 18px;
	}

	.components-color-palette__custom-color {
		border-width: 1px;
		border-color: rgb(204, 204, 204);
		border-radius: 4px;
		border-style: solid;
	}

	.gutenslider-arrow-style-select {
		height: 100%;
		display: flex;
    justify-content: center;

		span:first-of-type {
			padding-right: 2px;
		}

		svg .bg {
			display: none;
			background: transparent !important;
		}
	}

	.components-circular-option-picker {
		padding-bottom: 4px;
	}
}

.gutenslider-controls-general {

	.components-button-group {
		width: 100%;

		.components-button {
			// width: 50%;
			justify-content: center;
			font-size: 12px;
			margin-left: 0;
		}
	}
}

.eedee-icon-panel {
	.components-panel__body-title {
		.components-button {
			display: flex;
			flex-direction: row-reverse;
			justify-content: flex-end;
			align-items: center;

			svg.components-panel__icon {
				margin: 0 6px 0 0;
				fill: none;
			}
		}
	}
}

.gutenslider-controls-autoplay,
.gutenslider-controls-general {
	.components-base-control.components-toggle-control {
		// margin-bottom: 15px;
	}

	.components-toggle-control .components-form-toggle {
		margin-right: 9px;
	}

	.components-toggle-control .components-base-control__field .components-form-toggle {
		margin-right: 9px;
	}
}

.gutenslider-controls-arrows,
.gutenslider-controls-dots {
	.components-base-control__field {
		margin-top: 1rem;
	}
	.component-color-indicator {
		vertical-align: middle;
		margin-left: 10px;
	}
}

.gutenslider-btn-add-slide {
	display: flex;
	align-items: center;
	justify-content: center;
	outline: 1px dashed #8d96a0;
	width: 100%;
	color: #555d66;
	height: 60px !important;
	background: transparent;
	border-radius: 0;
	border: none !important;
	box-shadow: none !important;
	color: #555d66;
	font-size: 14px;
	height: 40px;
	justify-content: center;
	line-height: 31px;
	padding: 0 8px 0 8px;
	text-align: center;
	transition: color 0.2s ease;
	width: 100%;
	margin-top: 10px;

	&:hover {
		outline-color: #191e23 !important;
		border: none !important;
	}
}

.gutenslider-dimension-tabs {
	.components-tab-panel__tabs {
		margin-bottom: 10px;
		display: flex;
	}

	.gutenslider-device-tab {
		flex: 1;
	}
}

.gutenslider-device-tab {
	height: 35px;

	.gutenslider-tab-title {
		display: flex;
		height: 35px;
		padding-top: 7px;

		span {
			margin-left: 2px;
		}
	}
}

.gutenslider-device-tab.is-active {
	box-shadow: inset 0 -3px #007cba;
	font-weight: 600;
	position: relative;
	background: #f3f4f5;

	&.is-active::before {
		content: "";
		position: absolute;
		top: 0;
		bottom: 1px;
		right: 0;
		left: 0;
		border-bottom: 3px solid transparent;
	}
}

//style the edit button of gallery images
.block-library-gallery-item__inline-menu.inline-edit {
	bottom: -2px;
	left: -2px;
	top: auto;
	right: auto;

	.blocks-gallery-item__edit {
		padding: 0;
	}
}

.wp-block-eedee-block-gutenslide {
	.block-editor-block-list__layout {
		max-width: 100%;
		width: 100%;

		> * {
			max-width: 100%;
		}
	}

	//make mover visible
	.editor-block-list__layout.block-editor-block-list__layout {
		padding-left: 32px;
		padding-right: 32px;
	}
}

[data-type="eedee/block-gutenslider"][data-align="center"],
[data-type="eedee/block-gutenslider"][data-align="left"],
[data-type="eedee/block-gutenslider"][data-align="right"] {
	.editor-block-list__block-edit {
		min-width: 50%;
	}
}

