.wp-block-eedee-block-gutenslider.content-fixed .swiper-slide > div {
	height: 100%;
}

.gutenslider-controls .components-range-control__root {
	padding: 10px 0;
}

.wp-block-eedee-block-gutenslider .swiper-cube .swiper-wrapper > .swiper-slide-prev > .swiper-slide,
.wp-block-eedee-block-gutenslider .swiper-cube .swiper-wrapper > .swiper-slide-next > .swiper-slide,
.wp-block-eedee-block-gutenslider .swiper-cube .swiper-wrapper > .swiper-slide-active > .swiper-slide {
	visibility: visible;
}

.components-button.has-icon .dashicon {
	margin-right: 2px;
}

.eedee-bg-toolbar {
	display: flex;
	// justify-content: space-between;
	margin-bottom: 16px;

	.components-button.has-icon .dashicon {
		margin: 0 2px;
	}
}

.eedee-mediapicker {
	margin-bottom: 16px;
}

.eedee-icon-panel .components-button-group .components-button.is-primary,
.eedee-icon-panel .components-button-group .components-button:focus {
	z-index: 0;
}

.gutenslider-controls-spacing {
	.components-base-control__label {
		font-weight: bold;

		> span {
			font-weight: normal;
		}
	}

	.eedee-responsive-tab-panel {
		padding-left: 8px;
		margin-top: 8px;
	}
}

.gutenslide-controls-slide {
	.block-editor-link-control {
		min-width: unset;

		.block-editor-link-control__search-results {
			padding: 0;
		}

		.block-editor-link-control__search-item.is-current {
			// padding: 16px 0;
		}

		.block-editor-link-control__search-input {
			margin-bottom: 0;
		}

		.block-editor-link-control__search-input.block-editor-link-control__search-input input[type="text"] {
			// width: 100%;
			// margin: 16px 0;
		}

		.block-editor-link-control__search-actions {
			// right: 8px;
		}
	}
}

.eedee-divider-select {
	svg {
		width: 100%;
		height: 100%;
	}

	span.gutenslider-divider-top,
	span.gutenslider-divider-bottom {
		height: 100%;
		position: relative;
	}
}
