.components-panel__body--eedee-background-panel {

	.components-custom-gradient-picker__ui-line {
		align-items: flex-end;
	}

	.eedee-gradient-info,
	.eedee-color-info {
		font-style: italic;
	}

	.eedee-bg-toolbar {
		display: flex;
		margin-top: 8px;

		> div {
			padding: 3px;
		}

		button.components-toolbar__control {
			margin: 0;

			> svg {
				background: transparent;
			}

			&.is-pressed {
				color: #fff;

				> svg {
					padding: 0;
				}
			}
		}
	}
}
