
.eedee-responsive-link-button {
	position: absolute;
	right: 0;
	top: -6px;
	width: 64px;
	height: 16px;

	border-top-right-radius: 2px;
	border-top-left-radius: 2px;
	border: 1px solid #eee;
	cursor: pointer;
	background: transparent;

	&.is-active {
		background: #eee;
		border-color: #999;
	}
}

.eedee-responsive-tab-panel {
	position: relative;
	min-height: 42px;

	&.is-linked {
		.components-tab-panel__tabs-item {
			background: transparent !important;
		}

		.components-tab-panel__tabs {

			button.components-button {
				.eedee-responsive-icon {
					border-color: #999 !important;
					border-width: 1px;
					border-bottom-width: 1px;
				}
			}
		}

		.eedee-active-tab {
			background: transparent !important;
		}
	}

	.components-tab-panel__tabs {
		position: absolute;
		right: 0;
		top: 9px;

		> button.components-button {
			padding: 0;
			margin-left: 0px;
			height: auto;

			&:first-of-type {
				.eedee-responsive-icon {
					border-bottom-left-radius: 2px;
				}
			}

			&:last-of-type {
				.eedee-responsive-icon {
					border-bottom-right-radius: 2px;
				}
			}

			.eedee-responsive-icon {
				transition: color 0.3s, background-color 0.3s;
				padding: 2px 2.83px 0 2.83px;
				border: 1px solid #eee;
				color: #777;
			}

			.eedee-active-tab {
				background: #eee;
				border-color: #999;
				color: #444;
			}

			&:hover {
				.eedee-responsive-icon {
					border-color: #999;
					color: #555;
				}
			}
 		}
	}

	.eedee-responsive-tab label small {
		font-size: 10px;
	}

	//make more space for the following controls
	.components-toggle-control__label,
	.components-base-control__label {
		small {
			color: #999;
		}
	}

	.components-base-control__label {
		margin-bottom: 10px;
	}

	.components-toggle-control__label {
		margin-top: -1px;
		margin-bottom: 5px;
	}

	.components-toggle-control .components-base-control__field .components-form-toggle {
		margin-right: 9px;
	}
}
